import uniPopup from '@/components/uni-popup/uni-popup.vue'
import notice from '@/components/notice/index.vue'
import { mapState, mapMutations, mapGetters } from 'vuex'
var videoAd;
export default {
	data() {
		return {
			recordList: '',
			bargain_id: 0,
			dataList: '',
			id: '',
			curStartTime: '',
			day: '0',
			hour: '00',
			min: '00',
			second: '00',
		}
	},
	props:{
		page: {
			default: 'detail'
		}
	},
	components:{
		...mapGetters(['getLoginStatus','token']),
		notice,
		uniPopup
	},
	mounted() {
		this.$nextTick(function(){
			uni.showLoading({
				title:'加载中'
			});
			this.id = this.$parent.id;
			this.getDetailData();
			this.adGet();//这个地方就是调用了广告函数，然后直接展示
		})
	},
	onLoad() {
	},
	computed:{
		...mapGetters(['token','getLoginStatus']),
	},
	methods: {
		...mapMutations(['login','setToken']),
		getDetailData(){
			let url = 'detail';
			let data = {
				token: this.token, 
				active_id: this.id
			}
			if(this.page == 'invite'){
				url = 'invite-detail';
				data = {
					bargain_id: this.id
				}
			}
			this.$http.get( `/bargain_v1/bargain/${url}`,{params: {...data}})
				.then(res => {
					uni.hideLoading();
					console.log(res.data);
					if(res.data.code == 1){
						let data = res.data.data;
						this.dataList = data;
						this.bargain_id = data.bargain_id;
						this.curStartTime = data.expire_time;
						this.newPersonCountTime();
						this.getRecordList();
						if(this.page == 'invite'){
							this.$parent.setInviteData(data);
						}
					}else{
						uni.showToast({
							icon: 'none',
							title: res.data.msg
						});
						setTimeout(()=>{
							uni.switchTab({
								url: '../../pages/index/index'
							})
						},1500)
					}
				}).catch(err => {
					console.log(err);
				})
		},
		getRecordList(){
			this.$http.get('/bargain_v1/invite/list',{
				params: {
					token: this.token,
					bargain_id: this.bargain_id,
				}})
				.then(res => {
					console.log(res.data);
					if(res.data.code == 1){
						let data = res.data.data;
						this.recordList = data;
					}
				}).catch(err => {
					console.log(err);
				})
		},
		// 倒计时
		newPersonCountTime () {
		  // 获取当前时间
		  let date = new Date()
		  let now = date.getTime()
		  // 设置截止时间
		  // let endDate = new Date(this.curStartTime) // this.curStartTime需要倒计时的日期
		  // let end = endDate.getTime()
		  let end = this.curStartTime
		  // 时间差
		  let leftTime = end - now
		  // 定义变量 d,h,m,s保存倒计时的时间
		  if (leftTime >= 0) {
		    // 天
		    this.day = Math.floor(leftTime / 1000 / 60 / 60 / 24)
		    // 时
		    let h = Math.floor(leftTime / 1000 / 60 / 60 % 24)
		    this.hour = h < 10 ? '0' + h : h
		    // 分
		    let m = Math.floor(leftTime / 1000 / 60 % 60)
		    this.min = m < 10 ? '0' + m : m
		    // 秒
		    let s = Math.floor(leftTime / 1000 % 60)
		    this.second = s < 10 ? '0' + s : s
		  } else {
		    this.day = 0
		    this.hour = '00'
		    this.min = '00'
		    this.second = '00'
		  }
		  // 等于0的时候不调用
		  if (Number(this.hour) === 0 && Number(this.day) === 0 && Number(this.min) === 0 && Number(this.second) === 0) {
			  console.log('到点了')
			  this.newPersonGift = false;
		    return
		  } else {
		  // 递归每秒调用newPersonCountTime方法，显示动态时间效果,
		    setTimeout(this.newPersonCountTime, 1000)
		  }
		},
		bargain(){
			this.$http.get('/bargain_v1/invite/bargain',{params: {token: this.token, bargain_id: this.bargain_id}})
				.then(res => {
					console.log(res.data);
					if(res.data.code == 1){
						let data = res.data.data;
						if(data.result == 1){
							this.$refs.popup.open();
						}else{
							this.getDetailData();
						}
					}else{
						uni.showToast({
							icon: 'none',
							title: res.data.msg
						})
					}
				}).catch(err => {
					console.log(err);
				})
		},
		openVideoAd(){
			this.$http.get('/bargain_v1/bargain/time',{params: {token: this.token,bargain_id: this.bargain_id}})
				.then(res => {
					let data = res.data.data;
					console.log(res);
					if(res.data.code == 1){
						if(res.data.data.own_time != 0){
							if (videoAd) {
								console.log('打开激励视频');
								videoAd.load()
								  .then(() => {
									console.log('激励视频加载成功');
									videoAd.show().then(() => {
									  console.log('激励视频 广告显示成功')
									})
									.catch(err => {
									  console.log('激励视频 广告显示失败')
									})
								  })
								  .catch(err => {
									console.log('激励视频加载失败');
								  })
							 //  videoAd.show().catch(err => {
								// // 失败重试
								// videoAd.load()
								//   .then(() => videoAd.show())
							 //  })
							}
						}else{
							if(this.page == 'detail'){
								this.$parent.showCountPopup();
							}else{
								uni.showToast({
									title: '砍价次数不足'
								})
							}
						}
					}else{
						uni.showToast({
							title: res.data.msg || '请求失败'
						})
					}
				}).catch(err => {
					console.log(err);
				})
			
		},
		adGet(){
			let that = this;
			if (qq.createRewardedVideoAd) {
				videoAd = qq.createRewardedVideoAd({
				  adUnitId: 'f77ed470fc7ac19f34d0558baa78e500'
				})
				
				videoAd.onError(function(res){
				  console.log('videoAd onError',res)
				})
				videoAd.onLoad(function(res){
				  console.log('videoAd onLoad',res)
				})
				videoAd.onClose(function(status){
				  console.log('videoAd onClose',status)
				   if (status && status.isEnded || status === undefined) {
						// 正常播放结束，下发奖励
						// continue you code
						that.bargain();
						console.log(' 正常播放结束，下发奖励');
					  } else {
						  console.log('播放中途退出，进行提示');
						  uni.showToast({
							 icon:'none',
							title: '视频看完有才有奖励哦'
						  })
						// 播放中途退出，进行提示
					  }
				})
				
			}
		},
		toAwardsPage(){  //砍价成功弹窗去领奖
			this.$http.get('/bargain_v1/bargain/read-new',{params: {token: this.token,id: this.bargain_id}})
				.then(res => {
					let data = res.data.data;
					if(res.data.code == 1){
						uni.navigateTo({
							url: '../awards/index',
							success: ()=> {
								this.$refs.popup.close();
							}
						})
					}else{
						uni.showToast({
							title: res.data.msg
						})
					}
				}).catch(err => {
					console.log(err);
				})
		},
		userLogin(avatar,username){
			let that = this;
			uni.login({
			  provider: 'qq',
			  success: function (loginRes) {
				let code = loginRes.code;
				that.$http.post('/bargain_v1/user/login',{
						code: code,
						avatar: avatar,
						username: username
					},{
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						}
					})
					.then(res => {
						console.log(res.data);
						let data = res.data.data;
						that.setToken(data.token);
						uni.setStorageSync('uid',data.uid);
						that.openVideoAd();
					}).catch(err => {
						console.log(err);
					})
			  }
			});
		},
		bindGetUserInfo(e){
			console.log(e);
			if(!e.detail.iv){  
				uni.showToast({  
					icon: 'none',
					title: "登录失败！"  
				});  
				return false;  
			} else{
				let avatar = e.detail.userInfo.avatarUrl;
				let username = e.detail.userInfo.nickName;
				this.login(e.detail.userInfo);
				this.userLogin(avatar,username);
				
			}
		},
	}
}
